// Default colors
:root {
  --primary-color: #29a587;
  --background-color: #f2ede6;
  --text-color: #6b6a69;
  --button-text-color: #262c34;
  --heading-color: #262c34;
  --ligth-color: #ffffff;
  --dark-color: #262c34;
  --input-label-color: rgba(38, 44, 52, 0.5);
  --danger: #f44336;
  --warning: #f29345;
}

// Colors for ligth mode
html[data-theme="light"] {
  --background-color: #f2ede6;
  --text-color: #6b6a69;
  --button-text-color: #262c34;
  --heading-color: #262c34;
  --ligth-color: #ffffff;
  --dark-color: #262c34;
}

// Colors for dark mode
html[data-theme="dark"] {
  --background-color: #242a32;
  --text-color: #acadb0;
  --button-text-color: #ffffff;
  --heading-color: #ffffff;
  --input-label-color: rgba(255, 255, 255, 0.5);
}

